import { CoreModule } from './../core/core.module';
import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { PaymentConfirmationComponent } from './payment-confirmation.component';
import { routingPayment } from './payment-confirmation.routing';
import { CommonComponentsModule } from 'common/common.module';


@NgModule({
  imports: [
    routingPayment,
    CommonModule,
    CoreModule,
    CommonComponentsModule
  ],
  declarations: [
    PaymentConfirmationComponent
  ],
  exports:[PaymentConfirmationComponent]
})
export class PaymentConfirmationModule { }



