import { HttpSuccesFailureResponse } from './../core/http_wrapper/http_wrapper_response';
import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { HttpUserAuthUtilsService } from 'core/http_wrapper/http-userauth-utils.service';
import { PaymentConfirmationResponse } from 'core/models/payment_confirmation_resonse';
import { DatePipe } from '@angular/common';
import { FBPixel } from 'fbpixelCustomEvents';
import { ConfirmpopupComponent } from 'common/confirmpopup/confirmpopup.component';
import { Modal, overlayConfigFactory } from 'angular2-modal';
import { BSModalContext } from 'angular2-modal/plugins/bootstrap/src/bootstrap';
import { PakagesData } from 'core/models/packagesListResponse';
import { environment } from 'environments/environment';
import { LocalStorageService } from 'core/service/local-storage.service';
import { Constants } from '../core/constants';
import { UserLogin } from 'core/models/userloginresponse';
import { UserSession } from 'core/sessions';


declare var gtag: any;


@Component({
  selector: 'app-payment-confirmation',
  templateUrl: './payment-confirmation.component.html',
  styleUrls: ['./payment-confirmation.component.css']
})
export class PaymentConfirmationComponent implements OnInit,HttpSuccesFailureResponse {
transactionId:any;
sub:any;
transactionResponse:PaymentConfirmationResponse;
status:any;
paymentInfo:any;
packageInfo:PakagesData;
currencyCode:any;
autojoinClassCredits=0;
userDetails:UserLogin;
isLoggedIn:boolean;
transactiondate=new Date();
isFromSpecialPlan=false;
isPrime=UserSession.getDynamicElements(Constants.isPrime)=="Yes"?true:false;

  constructor( private _activatedRoute: ActivatedRoute,
    private _userAuthHttpService: HttpUserAuthUtilsService,
    private router: Router,private datePipe: DatePipe,
    private modal: Modal,) { }

  ngOnInit() {
    FBPixel.trackPurchase();
    this.isLoggedIn = LocalStorageService.getIsLoggedIn();
try{
  this.paymentInfo=JSON.parse(localStorage.getItem('payment_info'));
  this.packageInfo= JSON.parse(this.paymentInfo.package_info); 
 }catch(e){
   console.log(e)
 }
 
this.isFromSpecialPlan =  UserSession.isSpecialProgramClass(this.paymentInfo.class_info);
LocalStorageService.setIsFromSpecialPlan(false);

  this.userDetails = LocalStorageService.getUser();
    
    this.currencyCode = LocalStorageService.getCurrencyCode();
    this.sub = this._activatedRoute.params.subscribe(params => {
      
      if (params.hasOwnProperty('transactionId') && params['transactionId'] != null) {  
        this.transactionId = params['transactionId'];
        //if(this.isLoggedIn&&!this.isFromSpecialPlan){
          this._userAuthHttpService.getTransactionDetail(this.transactionId,this)

       // }
        
        
       
         
        localStorage.setItem(this.transactionId, this.transactionId);
      }else{
        this.router.navigate(['/classes']);
      }
    });

  }
 
  gtag_report_conversion(value:any) {
    try{
      gtag('event', 'conversion', {
        'send_to': 'AW-778903005/jI_9CK_mwI8BEN27tPMC',
        'currency': this.currencyCode,
        'order_id': value
      });
    }catch(e){
      
    }
    
    }

    callFbPixelEvents(transactionResponse) {
      try{
        if(transactionResponse.classDetailDto ) {
          FBPixel.membershipPurchaseEvent();
          FBPixel.joinClassEvent(transactionResponse.classDetailDto);
        }  // auto join Special and Normal class
  
        if(!transactionResponse.classDetailDto && this.paymentInfo && this.paymentInfo.purchase_type =='extention') {
          FBPixel.membershipExtensionPurchaseEvent();
        } // Membership extension 
  
        if(!transactionResponse.classDetailDto &&this.paymentInfo && this.paymentInfo.purchase_type !='extention') {
          FBPixel.membershipPurchaseEvent();
        } // normal package purchase
  
        
      }catch(e){
        console.log(e)


      }
      


    }

    onSuccess(type: any, responsedata: any) { 
      if (type === Constants.FindUserApi) {
       LocalStorageService.setUser(responsedata);
       this.userDetails = responsedata;

      }

      this.gtag_report_conversion(this.transactionId);
      this.transactionResponse=responsedata;
      this.status=this.transactionResponse.status
      try{
        if(environment.showPackagesForSpecialDiscount&&this.transactionResponse.status =='SUCCESS'&&this.packageInfo.promoResponseDto&&this.packageInfo.promoResponseDto.id==environment.specialDiscountPromoId&&this.paymentInfo.buyMembership=='true'){
          this.showPaymentConfirmation(Constants.specialDiscountMessage, Constants.specialDiscountTitle,this,'1100');
        }
      }catch(e){
        console.log(e)
 
      }

      this.callFbPixelEvents(responsedata);

     // let selectedClass: any = sessionStorage.getItem('joinClassDetails');

      try{
        if(sessionStorage.getItem('joinClassDetails')){
          let credits=JSON.parse(sessionStorage.getItem('joinClassDetails')).credit;
          this.autojoinClassCredits = credits==undefined?0:credits;
        }
      }catch(e){
        console.log(e)

      }
      if(!this.transactionResponse.expiryDate&&this.transactionResponse.classDetailDto){
        this.transactionResponse.validity= this.datePipe.transform(this.transactionResponse.classDetailDto.classDate, 'EEE, MMM d, y') +", "+ this.datePipe.transform(this.transactionResponse.classDetailDto.classDate+" "+this.transactionResponse.classDetailDto.fromTime, 'shortTime') +"-"+this.datePipe.transform(this.transactionResponse.classDetailDto.classDate+" "+this.transactionResponse.classDetailDto.toTime, 'shortTime'); 

      }
      else if(this.transactionResponse.expiryDate&&this.transactionResponse.classDetailDto){
        this.transactionResponse.validity=this.datePipe.transform(this.transactionResponse.expiryDate, 'EEE, MMM d, y');

      }
      else{
        this.transactionResponse.validity=this.datePipe.transform(this.transactionResponse.expiryDate, 'EEE, MMM d, y');

      }

    }
    onFailure(type: any, response: any) {
      switch(type){
        case Constants.TransactionDetailApi:
        this.router.navigate(['classes']);
         break;
      }

    }

    redirect(path){
      this.router.navigate([path]);

    }

    showPaymentConfirmation(message, title, parent?: any, errorCode?: any){
      return this.modal.open(ConfirmpopupComponent, overlayConfigFactory({ message, title , parent, errorCode}, BSModalContext));

    }
   get getUserBalance(){
      let balance = 0;
  
      try {
        if (this.userDetails.data.userPackageDetailDtoList !== undefined && this.userDetails.data.userPackageDetailDtoList.length > 0) {
        for (let i = 0; i < this.userDetails.data.userPackageDetailDtoList.length; i++) {
         balance = balance + this.userDetails.data.userPackageDetailDtoList[i].balance;
        }
       
        }
        }catch (e){
          console.log(e)

        }
      return balance;
    }
}



